  font-weight: 500;
  font-style: normal;
}

.nav-item {
  height: 40px;
}

a:hover {
  /* text-decoration: none !important; */
}

.info-icon {
  display: inline-block;
  position: relative;
  height: 40px;
  width: 40px;
  font-family: 'CMU Typewriter Regular';
  color: white;
  /* text-align: center; */
  font-size: 2rem;
  font-weight: bold;
}

.info-icon > * {
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.fire-icon {
  display: inline-block;
  position: relative;
  height: 40px;
  width: 40px;
}

.unit-btn {
  border-radius: 15%;
  background-color: #d9534f;
  color: white;
  text-align: center;
  font-size: 2rem;
  border: none;
  padding: 0.25rem 0.5rem;
}

.container > * > * {
  margin-top: auto;
  margin-bottom: auto;
}
.container > * > h1 {
  padding: 0.25rem 0.5rem;
}

.plate-group {
  outline: none;
}

.plate-group > * {
  display: inline-block;
  position: relative;
  border-radius: 50%;
  color: #fff;
  margin: 2.5px 0;
}

.plate-group__plate {
  background-color: gray;
  padding: 2rem;
  font-size: 1.25rem;
  border: none;
}

.plate-group__quantity {
  background-color: #000;
  font-size: 1rem;
  padding: 0.85rem;
  position: absolute;
  bottom: 0%;
  margin-left: -5px;
  opacity: 0;
}

