  color: white;
}

Form {
  margin: 0 auto;
  margin-top: 100px;
  width: 50%;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-control:focus {
  /* outline: 0px !important; */
  -webkit-appearance: none;
  box-shadow: none;
  border-bottom: 1px black solid;
}

.weight-input__form {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid black;
  text-align: right;
}

.weight-input__form::-webkit-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form:-ms-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form::-ms-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form::placeholder {
  color: #a9a9a9;
}

.weight-input__text {
  background-color: transparent;
  color: #000;
  border: none;
  margin-bottom: 16px;
}

.set-container {
  background-color: #d9534f;
  color: white;
  border-radius: 5px;
  padding: 5px 10px;
  margin: 10px;
  position: relative;
  width: 100%;
}

.percent-container {
  vertical-align: text-top;
}

.set {
  margin: auto 0;
  font-weight: bold;
}

.setList-container {
  width: 100%;
  height: 100%;
  /* background-color: gray; */
}

.setList {
  padding: 50px 0;
  width: 60%;
  min-width: 180px;
}

.center-vertically {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.unselectable {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.bg-black {
